// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -------------------\----------------------------------------------------------

body {
  background: #f0f0f0;
}

main {
  width: 100%; //  max-width: 1440px;
  margin: 0 auto;
  position: relative;
  box-shadow: 0 0 30px rgba(#000, .4);
  font-size: 0.8em;
}

.down-arrow {
  position: absolute;
  bottom: 10px;
  z-index: 99;
  left: 0;
  right: 0;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: 4px solid #fff;
  margin: 0 auto;
  transform: rotate(180deg);
  cursor: pointer;
  text-align: center;
  padding: 5px 0;

  &:before {
    content: "\e901";
    font-family: 'Icomoon';
    color: #fff;
    font-size: 36px;
    line-height: 0.75;
    -webkit-animation: 3s float infinite;
    animation: 3s float infinite;
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}
@keyframes float {
  0% {
    transform: translateY(-30%);
  }

  50% {
    transform: translateY(-80%);
  }

  100% {
    transform: translateY(-30%);
  }
}
@-webkit-keyframes float {
  0% {
    transform: translateY(-30%);
  }

  50% {
    transform: translateY(-80%);
  }

  100% {
    transform: translateY(-30%);
  }
}

.button_container {
  padding: 20px 0;
  display: block;
}

.hero {
  position: relative;
  background: url('../images/hero-bg.jpg') 50% 50% no-repeat;
  background-size: cover;
  object-fit: cover;
  height: 400px;
  overflow: hidden;
  @include breakpoint(map-get($breakpoints, 'medium')) {
    height: 100vh;
    height: calc(100vh - 80px);
    background-attachment: fixed;

    video {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  @media screen and (max-width: 1024px) and (orientation: landscape ) {
    background-attachment: scroll;
  }
  video {
    display: none;
  }


  video {
    @media screen and (max-width: 1024px) and (orientation: landscape ) {
      display: none;
    }
  }

  h1 {
    display: block;
    position: absolute;
    top: 40%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    max-width: 600px;
    z-index: 99999;
    margin: 0 auto;
    text-align: center;
    color: #fff;
    text-shadow: 0 2px 5px rgba($text-color, .8);
    text-transform: uppercase;
    line-height: 1.1em;
    letter-spacing: 1px;
    font-size: 60px;
    font-weight: bold;
    font-size: 30px;
    animation: 1.5s slideIn forwards;
    @include breakpoint(map-get($breakpoints, 'medium')) {
      letter-spacing: 1;
      font-size: 80px;
      line-height: 1.0em;
      max-width: 100%;
    }
  }
}
@keyframes slideIn {
  from {
    transform: translateY(-500%);
  }

  to {
    transform: translateY(0);
  }
}

.main-section {
  background: $brand-color;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  @include breakpoint(map-get($breakpoints, large)) {
    min-height: 600px;
    height: 100vh;
  }
}

.ceo-panel {
  background-image: url('../images/ceo-bg.jpg');
  background-position: 100% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 500px;
  position: relative;
  @include breakpoint(map-get($breakpoints, large)) {
    height: 100vh;
    min-height: 800px;
    @media all and (orientation: landscape) {
      background-attachment: scroll;
    }

  }

  .intro-text {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(#fff, .6);
    padding: 20px;
    @include breakpoint(map-get($breakpoints, large)) {
      background: none;
      top: 0;
      left: 0;
      width: 50%;
      padding: 40px;
    }

    span {
      display: block;
      font-size: 16px;
      @include breakpoint(map-get($breakpoints, large)) {
        padding-left: 10px;
        padding-right: 80px;
        font-size: 20px;
        transform: translateX(-100%);
        transition: .5s all 0.35s ease-in-out;
        @at-root .ceo-panel.in-view .intro-text span {
          transform: translateX(0);
        }
      }

      em {
        font-style: normal;
        display: block;
        padding: 20px;
      }
    }
  }

  h1 {
    color: $brand-color;
    font-size: 40px;
    font-weight: normal;
    line-height: 1;
    margin: 0;
    @include breakpoint(map-get($breakpoints, large)) {
      font-size: 120px;
      line-height: 1;
      transform: translateX(-100%);
      transition: .75s all ease-in-out;
      @at-root .ceo-panel.in-view .intro-text h1 {
        transform: translateX(0);
      }
    }
  }
}

.panel_bg {
  width: 100%;
  height: 100%;
  z-index: 0;
  min-height: 400px;
  max-height: 400px;
  background-position: 0 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  @include breakpoint(map-get($breakpoints, 'medium') + 1px ){
      background-attachment: fixed;

  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    background-attachment: scroll;
  }
  .section_one & {
    background-image: url('../images/thriving-1.jpg');
    background-position: 100% 50%;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .section_one .inner_panel--two & {
    background-image: url('../images/thriving-2.jpg');
    background-position: 100% 50%;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .section_two .inner_panel--one & {
    background-image: url('../images/chronic-1.jpg');
    background-position: 0 50%;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .section_three & {
    background-image: url('../images/kids-1.jpg');
    background-position: 100% 0;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .section_three .inner_panel--two & {
    background-image: url('../images/kids-2.jpg');
    background-position: 100% 50%;
    background-repeat: no-repeat;
    background-size: cover;
  }
  @include breakpoint(map-get($breakpoints, large)) {
    position: absolute;
    top: 0;
    left: 0;
    min-height: initial;
    max-height: initial; //    background-attachment: fixed;
  }
}

.inner_panel {
  opacity: 0;
  .panel_text {
  @media (max-width: map-get($breakpoints, large) - 1) {
    flex: 1 0 auto;
    height: auto;
  }

  }
  @media (min-width: map-get($breakpoints, large)) {
    .panel_text {
      transform: translateY(-100%);
      transition: 0.35s all ease-in-out;
    }

    .main-section:nth-of-type(even) & .panel_text {
      transform: translateY(100%);
    }

    .in-view & .panel_text {
      transform: translateY(0);
      opacity: 1;
    }

    .main-section.in-view:nth-of-type(even) & .panel_text {
      transform: translateX(0);
    }

    .panel_text p {
      transform: translateX(-100%);
      transition: 0.3s all ease-in-out;
      opacity: 0;
      transition-delay: 0s;

      &:nth-of-type(1) {
        transition-delay: 0.1s;
      }

      &:nth-of-type(2) {
        transition-delay: 0.2s;
      }

      &:nth-of-type(3) {
        transition-delay: 0.3s;
      }

      .main-section:nth-of-type(even) & {
        transform: translateX(100%);
      }
    }
  }

  .panel_bg {
    opacity: 0;
    transition: 0.3s all ease-in-out;
    @media (max-width: map-get($breakpoints, large) - 1px) {
      display: none;
    }
  }

  &--active {
    opacity: 1;

    .panel_text p {
      transform: translateX(0);
      opacity: 1;

      .main-section:nth-of-type(even) & {
        transform: translateX(0);
      }
    }

    .panel_bg {
      opacity: 1;
      display: block;
    }
  }
}

.panel_text {
  background: rgba($brand-color, .8);
  color: #fff;
  padding: 40px 30px;
  z-index: 1;
  position: relative;
  height: 100%;
  flex: 1 0 auto;
  @include breakpoint(map-get($breakpoints, large)) {
    padding: 80px 40px;
    position: absolute;
    top: 0;
    left: 0;
    width: 40%;
    max-width: 740px;
  }

  .main-section:nth-of-type(even) .inner_panel & {
    @include breakpoint(map-get($breakpoints, large)) {
      left: auto;
      right: 0;
    }
  }

  .main-section:nth-of-type(even) .inner_panel--active & {
    @include breakpoint(map-get($breakpoints, large)) {
      //  transform: translateX(0);
    }
  }

  h2 {
    font-size: calc(2rem + 1vw);
    line-height: 1;
    text-transform: uppercase;
    font-weight: bold;

    .section_two & {
      font-size: calc(1.2rem + 1vw);
    }

    .section_three & {
      font-size: calc(1.2rem + 1vw);
    }

    em {
      color: $red-color;

      .section_three & {
        display: block;
        font-size: calc(2rem + 1vw);
      }
    }
  }
}

.info_g {
  background: #fff;
  padding: 40px 20px;
  @media (max-width: map-get($breakpoints, large) - 1px) {
    min-height: 400px;
  }
  @include breakpoint(map-get($breakpoints, large)) {
    position: absolute;
    top: 0;
    left: 0;
    width: 60%;
    min-width: calc(100% - 740px);
    height: 100%;
    padding: 80px 80px 80px 40px;
  }

  .info-text {
    font-weight: bold;
    color: $red-color;
    font-size: calc(1.2em + 1vw);
    line-height: 1;
    max-width: 700px;

    span {
      color: $brand-color;
      font-size: 0.5em;
      line-height: 1;
      display: block;
      max-width: 650px;
      margin: 10px 0px;
      margin-top: 25px;
    }
  }

  &--circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform-origin: 50% 50%;
    @at-root .inner_panel--active .info_g--circle {
      transform: scale(0);
      animation: 1s grow forwards;
    }
    @media (max-width: map-get($breakpoints, large) - 1px) {
      position: relative;
    }
  }

  &--house {
    display: block;
    background: url('../images/house.svg') 50% 50% no-repeat;
    height: 50%;
    width: 50%;
    background-size: contain;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    width: 280px;
    @include breakpoint(map-get($breakpoints, large)) {
      width: 500px;
    }
    @media (max-width: map-get($breakpoints, large) - 1px) {
      position: relative;
      display: block;
      width: 280px;
      height: 280px;
      top: 0;
      left: 0;
      transform: translate(0%, 0);
      margin: 0 auto;
    }
  }

  &--text {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    font-size: 3em;
    line-height: 1.2em;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    transform: translateY(-30%) translateX(-50%);
    text-align: center;
    @include breakpoint(map-get($breakpoints, large)) {
      font-size: 4.25em;
      line-height: 1em;
    }
  }
}
@keyframes grow {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

.main-section .inner_panel {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  @media (max-width: map-get($breakpoints, large) - 1px) {
    .panel_bg {
      order: 1;
    }

    .panel_text {
      order: 2;
    }
  }
}

.inner_panel:not(.inner_panel--active) {
  @media (max-width: map-get($breakpoints, large) - 1px) {
    display: none;
  }
}

.section {
  padding: 40px 20px;
  background: #fff;
  min-height: 40px;
}

.tabPanel:not(.active) {
  display: none;
}

.tabs-section {
  margin: 0;
  padding: 0;
}

.tabsList {
  $shadow-width: 3px;
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  li {
    width: 33.33%;
    display: block;
    text-align: center;
    display: flex;
    height: auto;
    &:hover a:not(.active) {
      color: $yellow-color;
    }
    &.tab-one a.active {
      box-shadow: inset -#{$shadow-width} 0 0 $red-color;
    }

    &.tab-two a.active {
      box-shadow: inset -#{$shadow-width} 0 0 $red-color, inset #{$shadow-width} 0 0 $red-color;
    }

    &.tab-three a.active {
      box-shadow: inset #{$shadow-width} 0 0 $red-color;
    }
  }

  a {
    display: block;
    width: 100%;
    background: $brand-color;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    box-shadow: inset 0 -#{$shadow-width} 0 $red-color;
    font-size: 14px;
    text-align: center;
    height: 60px;
    padding: 10px 20px;
    transition: .25s color ease-in-out;
    text-decoration: none;
    @include breakpoint(map-get($breakpoints, medium)) {
      font-size: calc(1rem + 1vw);
      line-height: calc(1rem + 1.01vw);
      padding: 40px;
      height: auto;
    }

    &.active {
      background: #fff;
      color: $red-color;
    }
  }
}

.tabPanel {
  padding: 40px;
  background: #fff;
  display: none;

  &.tab-one {
    justify-content: space-between;
    max-width: 1400px;
    margin: 0 auto;
    opacity: 0;
    transition: .3s all ease-in-out;
    &.in-view.active {
      opacity: 1;
    }
  }

  &.tab-two {
    //    background: brown;
    background: #fff;
  }

  &.tab-three {
    background: #fff;
    position: relative;
    padding-bottom: 15px;

    .read-more {
      position: absolute;
      bottom: 0;
      width: 120px;
      margin: 0 auto;
      background: $red-color;
      font-weight: bold;
      text-transform: uppercase;
      color: #fff;
      text-transform: uppercase;
      width: 100%;
      padding: 20px;
      left: 0;
      transition: 0.25s all ease-in-out;

      &:hover {
        background: darken($red-color, 10%);
      }
    }
  }

  &.active {
    display: block;
    width: 100%;
    @include breakpoint(map-get($breakpoints, 'medium')) {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
    }
  }
}

.jqplot-target {
  background: none;
  border: 0;
}

.jqplot-base-canvas,
.jqplot-grid-canvas,
.jqplot-series-shadowCanvas {
  display: none;
}

.jq-plot-shadowCanvas {
  display: none;
}

.jqplot-data-label {
  color: #fff;
  font-weight: bold;
  font-family: $text-font-stack;
}

.jqplot-data-label:after {
  content: '%';
}

.chart-1,
.chart-2 {
  display: block;
  max-width: 400px;
  width: 100%;
  margin: 0 auto;

  > * {
    margin: 15px 0;
  }
  @include breakpoint(map-get($breakpoints, 'medium')) {
    max-width: 400px;
    width: 50%;
  }
}

svg {
  .ct-slice-donut {
    stroke-width: 40px;
  }

  .ct-series {
    path {
      stroke-width: 0;
      animation: 0.3s fillstroke;
      transform-origin: 50% 50%;
    }

    text {}
  }

  .ct-series-a .ct-slice-donut {
    stroke: $brand-color;
  }

  .ct-series-b .ct-slice-donut {
    stroke: #fcb945;
  }

  .ct-series-c .ct-slice-donut {
    stroke: #7fa8cd;
  }

  .ct-series-d .ct-slice-donut {
    stroke: #f15c2a;
  }

  .ct-label {
    font-size: 20px;
    font-weight: bold;
    color: #fff;
    fill: #fff;
  }
}
@keyframes fillstroke {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

footer {
  background: $brand-color;
  color: #fff;
  padding: 40px;
  position: relative;
  z-index: 1;
  box-shadow: 0 -4px 0 0 $yellow-color;
  text-align: center;
  @include breakpoint(map-get($breakpoints, 'medium')) {
    text-align: left;

  }

  .back-to-top {
    position: absolute;
    top: -20px;
    right: 40px;
    box-shadow: 0 0 0 4px $yellow-color;
    border-radius: 4px;
    color: #fff;
    padding: 5px 10px;
    background: $brand-color;
    height: 40px;
    width: 40px;
    transition: .25s all ease-out;
    &:hover {
      background: $yellow-color;
      &:before {
        color: $brand-color;
      }
    }
    &:before {
      content: "\e901";
      font-family: 'Icomoon';
      color: #fff;
      font-size: 28px;
      line-height: 1em;
    }
  }

  .footer-wrapper {
    max-width: 1400px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

   >  div {
      display: block;
      width: 100%;
      margin: 20px 0;
      @include breakpoint(map-get($breakpoints, 'medium')) {
        flex-basis: 33.33%;
        padding: 0 20px;
      }
    }
  }

  form {
    position: relative;
    label {
      display: none;
    }
  }

  input {
    display: block;
    width: 100%;
    height: 40px;
    border-radius: 0;
    -webkit-appearance: none;
    padding-right: 90px;
  }

  input[type="submit"] {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 32px;
    right: 5px;
    width: 80px;
    background: $brand-color;
    color: #fff;
    transition: 0.3s all ease-in-out;
    border: 0;
    box-shadow: 0;
    font-size: 10px;
    -webkit-appearance: none;
    border-radius: 0px;
    cursor: pointer;
    padding: 5px 0;

    &:hover {
      background: rgba($brand-color, .8);
    }
  }
}

.chart-1,
.chart-2 {
  img {
    height: 80px;
    display: block;
    width: 100%;
  }
}

.donors {
  max-width: 1400px;
  margin: 40px auto;
  transition: 0.3s all ease-in-out;

  .tab-three & {
    max-height: 100vh;
    overflow: hidden;
    padding-bottom: 40px;
  }

  &.active {
    height: auto;
    max-height: initial;
  }

  .toc-logo {
    display: block;
    clear: both;
    margin-left: -20px;
    width: 100%;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    ul {
      margin: 20px 0;
    }
  }

  > ul {
    column-count: 2;
    column-gap: 20px;

    .tab-three & {
      column-rule: 1px outset $brand-color;

      .header {
        font-size: 18px;
        font-weight: bold;
      }
    }
    @include breakpoint(map-get($breakpoints, 'medium')) {
      column-count: 3;
      column-span: 33.33%;

      .tab-three & {
        column-count: 4;
        column-span: 25%;
      }
    }
  }

  ul {}

  li {
    font-size: 14px;
    font-style: italic;
    font-weight: bold;
  }

  .header {
    text-transform: uppercase;
    color: $red-color;
    text-transform: uppercase;
    font-style: normal;
    font-weight: 600;
  }

  .doner-footer {
    font-style: normal;
    color: $brand-color;
  }
}

.community,
.corporate {
  margin: 0;
  padding: 0 30px;
  text-align: left;

  .donors {
    padding: 40px 0;
    margin: 0 auto;
  }

  ul {
    text-align: left;
    list-style: none;
    padding: 0;
    margin: 0;
    @at-root .community ul {
      color: $yellow-color;
    }

    > ul {
      column-count: 2;
      @include breakpoint(map-get($breakpoints, 'medium')) {
        column-count: 4;
      }
    }
  }
}

.corporate {
  background: #ffeccc;
  min-height: 100vh;

  .partners-header {
    background: $brand-color;
  }
}

.community {
  background: url('../images/community-bg.jpg') 0 0 no-repeat;
  background-size: cover;
  min-height: 100vh;
  @include breakpoint(map-get($breakpoints, large)) {
    background-attachment: fixed;
  }
  .partners-header {
    background: $yellow-color;
    color: $brand-color;
  }
}

.partners {
  margin: 0 auto;
  text-align: center;
}

.partners-header {
  margin: 0 auto;
  text-transform: uppercase;
  text-align: center;
  font-size: calc(1em + 1vw);
  color: #fff;
  font-weight: bold;
  padding: 10px 40px;
  max-width: 100%;
  display: inline-block;
  box-shadow: 0 3px 3px rgba(#000, .2);
}

.address {
  text-align: center;
  padding: 0 40px;
  font-size: 25px;
  font-weight: bold;

  span {
    display: block;
    line-height: 1.2;

    &:nth-child(2) {
      font-size: 21px;
    }

    &:nth-child(3) {
      font-size: 18px;
    }
  }
}

.social-links {
  list-style: none;
  font-size: 48px;
  text-align: center;
  margin: 0;
  padding: 0;

  ul {}
  @include breakpoint(map-get($breakpoints, medium)) {
    text-align: right;
  }

  li {
    display: inline-block;
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }

  span {
    display: none;
  }

  a {
    color: #fff;
    transition: 0.25s all ease-in-out;

    &:hover {
      color: $yellow-color;
    }
  }
}

.connect-with-us {
  text-transform: uppercase;
  line-height: 1.2em;

  form {
    width: 100%;
    @include breakpoint(map-get($breakpoints, medium)) {
      max-width: 270px;
    }
  }

  span {
    display: block;
  }

  .large-text {
    font-weight: bold;
    font-size: 33px;
  }

  .small-text {
    font-weight: 100;
    font-size: 21px;
  }
}


.lightbox-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(#000, .9);
  z-index: 99999;
  display: none;
  &.active {
    display: block;
  }
  iframe {
    position: absolute;
    top: 50%;
    transform: scale(0) translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
    display: block;
    max-width: 800px;
    width: 100%;
    height: calc(100% / 2);
    border: 0;
    transition: .35s all ease-in-out;
    @at-root .lightbox-container.active iframe {
      transform: scale(1) translateY(-50%);
    }
  }
}


.donate-button {
    padding: 10px 20px;
    background: $yellow-color;
    color: #fff;
    display: block;
    margin: 0 auto;
    transition: .3s all ease-in-out;
    line-height: 1em;
    box-shadow: 2px 2px 2px rgba(#000, .2);
    transition: .3s all ease-in-out;
    font-size: 16px;
    margin-bottom: 15px;
    max-width: 180px;
    box-shadow: 0 0 0 0 $yellow-color;
    text-transform: uppercase;
    &:hover {
      box-shadow: 0 0 0 4px $yellow-color;
      background: none;
      color: #fff;
    }
}

-webkit-fullscreen-ancestor iframe {
  top: 0 !important;
  transform: translateY(0) !important;
}
iframe:-webkit-full-screen {
  top: 0 !important;
  transform: translateY(0) !important;
}
