// -----------------------------------------------------------------------------
// This file contains all styles related to the button component.
// -----------------------------------------------------------------------------

button {
  background: none;
  border: none;
  box-shadow: none;
  cursor: pointer;
  font-size: 14px;
  letter-spacing: 0.04em;
}

.toggle-active {
  box-shadow: inset 0 0 0 1px #fff;
  padding: 15px;
  color: #fff;
  transition: .3s all ease-in-out;
  cursor: pointer;
  &:hover {
    background: #fff;
    color: $brand-color;
  }
  &.back-button {
    box-shadow: none;
    color: $yellow-color;
    padding: 20px 0;
    &:hover {
      background: none;
      color: $red-color;
    }
    &:before {
      content: '<';
      display: inline-block;
      margin-right: 5px;
    }
  }
}

.button-alt {
    padding: 15px;
    color: #fff;
    background: $red-color;
    box-shadow: 2px 2px 2px rgba(#000, .2);
    transition: .3s all ease-in-out;
    &:hover {
      box-shadow: 0 0 2px -2px rgba(#000, .5);
    }
}
