@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?ievtwz');
  src:  url('../fonts/icomoon.eot?ievtwz#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?ievtwz') format('truetype'),
    url('../fonts/icomoon.woff?ievtwz') format('woff'),
    url('../fonts/icomoon.svg?ievtwz#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-close:before {
  content: "\e900";
}
.icon-angle-up:before {
  content: "\e901";
}
.icon-facebook:before {
  content: "\ea90";
}
.icon-instagram:before {
  content: "\ea92";
}
.icon-twitter:before {
  content: "\ea96";
}
