// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

header {
  background: $brand-color;
  box-shadow: 0 4px 0 $red-color;
  position: relative;
  z-index: 99;
  height: 80px; //  height: 60px;
  @include breakpoint(map-get($breakpoints, 'large')) {
    height: 80px;
  }

  .logo-wrapper {
    position: absolute;
    left: 0;
    width: 120px;
    padding: 10px 10px 10px 20px;
    @include breakpoint(map-get($breakpoints, 'medium')) {}

    img {
      display: block;
      width: 100%;
      max-width: 100%;
    }
  }

  .siteLogo {}

  .siteLogo_tagline {
    float: left;
    display: none;
    @include breakpoint(map-get($breakpoints, 'medium') + 100) {
      display: block;
    }
  }

  h1 {
    margin: 0;
    color: #fff;
    font-size: 18px;
    text-transform: uppercase;
    padding: 10px 0 10px 120px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    @include breakpoint(map-get($breakpoints, 'medium')) {
      font-size: 30px;
    }

    span {
      @include breakpoint(map-get($breakpoints, 'medium')) {
        font-size: 50px;
        top: 5px;
        position: relative;
      }
    }
  }

  .wrapper {
    position: static;
  }
}

.mainNav_trigger {
  position: absolute;
  right: 40px;
  top: 50%;
  z-index: 99999;
  cursor: pointer;
  width: 60px;
  height: 45px;
  transform: rotate(0deg) translateY(-50%) scale(0.6);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
  &:hover span {
    background: $yellow-color;
  }
  .off-canvas & {
    top: 40px;
    transform: translateY(0) scale(0.6);
  }
  @media (max-width: map-get($breakpoints, 'medium') - 1px) {
    top: 20px;
    right: 0;
    transform: scale(0.5) rotate(0);
    .off-canvas & {
      top: 20px;
    }
  }
  span {
    display: block;
    position: absolute;
    height: 9px;
    width: 100%;
    background: #fff;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.25s ease-in-out;
    -moz-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;

    &:nth-child(1) {
      top: 0;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    &:nth-child(2) {
      top: 18px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    &:nth-child(3) {
      top: 36px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }
  }
  .off-canvas-active & span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -3px;
  left: 8px;
}

.off-canvas-active & span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

.off-canvas-active & span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 39px;
  left: 8px;
}
}

.off-canvas {
  position: fixed;
  z-index: 9999999;
  width: 100%;
  top: 0;
  height: 100vh;
  transform: translateY(-120%);
  transition: 0.25s all ease-in-out;
  background: $brand-color;

  ul {
    margin: 0;
    padding: 0;
    position: fixed;
    margin: 0 auto;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    text-align: center;
  }

  li {
    list-style: none;
    margin: 20px;
    a {
      color: #fff;
      text-transform: uppercase;
      font-weight: bold;
      &:hover {
        color: $yellow-color
      }
    }
  }

  &-active & {
    transform: translateY(0);
  }
}
